@import "./vars";

#Header {
  $drawer-width: 300px;

  .drawer>div {
    width: $drawer-width;

    .drawer-content {
      .activated{
        color: #fff;
        background-color: $secondary;
        svg{
          color: #fff;
        }
      }
      .avatar {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 20px 0;
        img {
          box-shadow: #555 0 0 5px 1px;
          border-radius: 100%;
          width: 175px;
          height: auto;
        }
      }
      .name{
        margin: 10px 0 20px 0;
      }
      .logo{
        margin: 25px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
          width: 50%;
          height: auto;
        }
      }
      .developer{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 20px 0;
      }
    }
  }

  @media only screen and (max-width: 960px) {
    $drawer-width: 200px;
    
    .drawer>div {
      position: relative;
      width: 100% !important;
      width: $drawer-width;

      .drawer-content {
        .avatar {
          img {
            width: 200px;
            height: auto;
          }
        }
      }
    }
  }
}