a{
  text-decoration: none;
  color: inherit;
}

.App {
  box-sizing: border-box;
  display: block;
}

.App-header {
  display: block;
}

.App-main {
  box-sizing: border-box;
  background-color: #ededed;
  min-height: 100vh;
  padding: 20px 35px;
  margin-bottom: 25px;
  width: calc(100% - 300px);
  margin-left: auto;
}

.App-footer {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  position: fixed;
  bottom: 0;
  z-index: 9999;
  color: #ededed;
  background-color: #0d3e69;
}

.sweet-loading {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 960px){
  .App-main{
    width: 100%;
    padding: 15px 20px;
  }
}